.headerContainer {
  display: flex;
  justify-content: space-around;
}
.logoContainer {
  display: flex;
}
.logoContainer img {
  height: auto;
  width: 42px;
}
.logoContainer h1 {
  font-family: Georgia, Times New Roman, Times, serif;
}
.navMenu {
  max-width: 500px;
}
.nav {
  display: flex;
  justify-content: space-between;
}
.navLink a {
  text-decoration: none;
  color: rgb(35, 161, 35);
}
.navLink {
  margin-right: 20px;
  margin-top: 20px;
  list-style: none;
}
.navLinkGetStart {
  margin-top: 10px;
  border: 1px solid rgb(35, 161, 35);
  border-radius: 32px;
  padding: 10px;
}
.navLinkGetStart a {
  text-decoration: none;
  color: rgb(35, 161, 35);
}
